import 'mocha';
import axios from 'axios';
import { assert } from 'chai';
import MockAdapter from 'axios-mock-adapter';
import configureStore, { MockStoreCreator, MockStoreEnhanced } from 'redux-mock-store'
import thunk from 'redux-thunk'
import { updateName } from '../../src/web/actions/userActions';
import { AnyAction } from 'redux';
import { ADD_INFO } from '../../src/web/actions/notificationsActions';

const mockStoreCreator: MockStoreCreator = configureStore([thunk]);

function getStore(): MockStoreEnhanced {
    return mockStoreCreator({});
}

describe('Async Actions', function() {
    let mockStore: MockStoreEnhanced<{}, any>;
    let mockAxios: MockAdapter;

    before(function() {
        mockAxios = new MockAdapter(axios);
    });

    after(function() {
        mockAxios.restore();
    });
    
    describe('User async actions', function() {
        beforeEach(function () {
            mockStore = getStore();
            mockAxios.reset();
            mockAxios.onAny().reply(200, {})
        });
        it('should handle callback and set info ' +
           'on successful post request to /api/v1/user/update/name', function() {
                let name : false | string = false;
                mockStore
                    .dispatch(updateName('Adrian', () => name = 'Adrian'))
                    .then(() => {
                        return assert.strictEqual(name, 'Adrian');
                    }).then(() => {
                        const actions: AnyAction[] = mockStore.getActions();
                        return assert.deepStrictEqual(actions, [{
                            type: ADD_INFO
                        }]);
                    }).catch((err: any) => {
                        // So that the error is thrown in an anonymous function, not a promise.
                        // https://stackoverflow.com/questions/30715367/why-can-i-not-throw-inside-a-promise-catch-handler#answer-30741722
                        setTimeout(() => { throw err; });
                    });
        });
        it('should set an error on failed post request to /api/v1/user/update/name', function() {

        });
        it('should update email on successful post request to /api/v1/user/update/email');
        it('should set an error on failed post request to /api/v1/user/update/email')
        it('should set info on successful post request to /api/v1/user/update/password');
        it('should set an error on failed post request to /api/v1/user/update/password')
    });
    describe('Channels async actions', function () {
        beforeEach(function () {
            mockStore = getStore();
        });
        it('should fetch channels');
        it('should retrieve channel messages');
        it('should delete channel');
        it('should create new channel');
    });
    describe('Socket async actions', function () {
        beforeEach(function () {
            mockStore = getStore();
        });
        it('should initialize websocket connection');
    });
    describe('Chat Users async actions', function () {
        beforeEach(function () {
            mockStore = getStore();
        });
        it('should fetch all users');
        it('should create a new user');
        it('should edit the user');
        it('should delete the user');
    });
})